.hero {
    padding: 400px 0 0;
}

.hero__slider-wrapper {
    padding-left: 48px;
}

.hero-slider {
    position: relative;
}

.hero-slider__slide {
    display: flex;
}

.hero-slider__text-content {
    max-width: 380px;
}

.hero-slider__title {
    display: flex;
    font-weight: normal;
    font-size: 22px;
    line-height: 23px;
    letter-spacing: -0.015em;
    color: #fff;
}

.hero-slider__title strong {
    margin-right: 25px;
    font-weight: normal;
    font-size: 85px;
    line-height: 89px;
    letter-spacing: -0.015em;
    color: #1EA4DE;
}

.hero-slider__title span {
    padding-top: 10px;
    opacity: 0.8;
}

.hero-slider__img {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 702px;
    height: 578px;
}

.hero-slider__img img {
    width: 100%;
    height: 100%;
    max-width: 100%;
}

.hero-slider__pagination {
    position: absolute;
    top: 10px;
    left: -34px;
    transform: translateX(-100%);
}

.hero-slider__dot {
    position: relative;
    display: block;
    margin-top: 12px;
    border: 1px solid rgba(255, 255, 255, 0.8);
    border-radius: 50%;
    width: 14px;
    height: 14px;
}

.hero-slider__dot:first-of-type {
    margin-top: 0;
}

.hero-slider__dot--active::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 10px;
    height: 10px;
    background: radial-gradient(50% 50% at 50% 50%, #FFFFFF 51.04%, rgba(255, 255, 255, 0) 100%);
}