.glow {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: var(--bg);
}

.glow--right {
    width: 54.27%;
    height: 56.85%;
    top: 21.57%;
    right: 0;
    bottom: unset;
    left: unset;
}

.triangles {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: 0.6;
}

.triangles::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: radial-gradient(50% 50% at 50% 50%, rgba(0, 0, 0, 0) 50%, #060D15 125%);
}

.triangles svg {
    stroke-linejoin: round;
    stroke-linecap: round;
}

.triangles__item {
    position: absolute;
    top: var(--tTop);
    left: var(--tLeft);
    width: var(--tWidth);
    height: var(--tHeight);
    transform: var(--tRotate);
    filter: var(--tBlur);
    opacity: 0.8;
}