.our-team {
    padding-top: 15%;
}

.our-team__title {
    position: relative;
    top: 40px;
}

.our-team__slider-wrapper {
    padding: 0 112px;
}

.team-slider__slide {
    position: relative;
    display: block;
    margin: 0 37px;
    padding-top: 105px;
    width: 315px;
    opacity: 0.08;
    transition: padding 0.35s ease;
}

.slick-current {
    opacity: 1;
    padding-top: 0;
}

.slick-current .team-slider__text-content {
    visibility: visible;
    opacity: 1;
    transform: translateX(0);
}

.team-slider__text-content {
    position: absolute;
    top: 0;
    right: -276px;
    z-index: 10;
    width: 200px;

    display: flex;
    flex-direction: column;

    margin-left: 75px;
    padding-top: 168px;

    visibility: hidden;
    opacity: 0;
    transform: translateX(100px);

    transition: 
        visibility 0.4s ease,
        opacity 0.4s ease,
        transform 0.4s ease 0.2s;
}

.team-slider__position {
    order: -1;

    font-weight: bold;
    font-size: 18px;
    line-height: 22px;
    color: #57CEFF;
}

.team-slider__member {
    font-weight: bold;
    font-size: 50px;
    line-height: 61px;
    color: #fff;
}

.team-slider__img-wrapper {
    position: relative;
    overflow: hidden;
    border-radius: 40px;
    width: 100%;
    height: 477px;
    box-shadow: inset 0px 2px 6px rgba(255, 255, 255, 0.25);
}

.team-slider__img-wrapper::after {
    content: "";
    position: absolute;
    z-index: -1;
    top: 50%;
    left: 50%;

    border-radius: 40px;
    width: 317px;
    height: 479px;
    background: linear-gradient(45deg, #0A111A, #6A6988 20%, #858CBE, #87D2FD 100%);
    transform: translate(-50%, -50%);
}

.team-slider__img-wrapper  img {
    width: 100%;
    height: 100%;
    max-width: 100%;
    object-fit: cover;
}

.our-team__slider-control {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
}

.our-team__slider-btn {
    border: 1px solid #1676AA;
    border-radius: 50%;
    width: 56px;
    height: 56px;
    background: none;
    cursor: pointer;
}

.our-team__slider-btn--prev {
    padding-right: 4px;
}

.our-team__slider-btn--next {
    padding-left: 4px;
}

.our-team__slider-btn svg {
    padding-top: 4px;
    width: 11px;
    height: 22px;
}