.header__content {
    padding-top: 25px;
    display: flex;
    justify-content: space-between;
}

.header__nav {
    padding-top: 5px;
    display: flex;
    color: #fff;
}

.header__link {
    display: block;
    padding: 15px;
    font-weight: bold;
    font-size: 18px;
    line-height: 19px;
    text-align: right;
    letter-spacing: -0.015em;
}

.header__link--framed {
    margin-left: 5px;
    border: 1px solid #1676AA;
    border-radius: 30px;
    padding: 14px 30px;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.logo {
    width: 223px;
    height: 47px;
}

.logo img {
    width: 100%;
    height: 100%;
    max-width: 100%;
}
